.info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    padding-top: 2.5%;
}

.info-section,.profile-section{
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1;
}

.info-section{
    align-self: center;
    text-align: left;
    padding-right: 5%;
}

#profile{
    border-radius: 50%;
    align-self: center;
    max-width: 50%;
}


@media screen and (max-width: 500px) {
    .info {
      flex-direction:column;
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      padding-left:10%;
      padding-right:10%;
      padding-top: 2.5%;
      margin-right: 0px;
      margin-left: 0px;
    }
    .info-section{
        padding-top:5%;
    }
  }